/* Header */

.header {
  display: block;
  width: 100%;
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  right: 0;
  padding: 1rem 1.5rem;
  background: $white;
  border-bottom: 1px solid $border;
  font-size: 0;
  line-height: 0;
  &__logo {
    display: inline-block;
    img {
      display: block;
      height: 3rem;
      width: auto; }
    h1 {
      margin-bottom: 0; } }
  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between; } }

