/* General */

.btn {
  display: inline-block;
  @include font-400();
  color: $text;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.4375rem 1rem;
  font-size: 1rem;
  line-height: 1.5em;
  border-radius: 0.25rem;
  transition: all .3s ease-in-out;
  &:hover {
    color: $primary;
    text-decoration: none; }
  &:focus, &.focus {
    outline: none;
    box-shadow: none; }
  &.disabled, &:disabled {
    opacity: .65;
    pointer-events: none; }
  &:not(:disabled):not(.disabled) {
    cursor: pointer; }
  // primary button
  &--primary {
    color: $white;
    background-color: $primary;
    border-color: $primary;
    &:hover {
      color: $white;
      background-color: darken($primary, 5%);
      border-color: darken($primary, 5%); }
    &:focus, &.focus {
      color: $white;
      background-color: darken($primary, 8%);
      border-color: darken($primary, 8%); }
    &.disabled, &:disabled {
      color: $muted;
      background-color: $white;
      border-color: $border;
      opacity: 1; }
    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active {
      color: $white;
      background-color: darken($primary, 5%);
      border-color: darken($primary, 5%); } }
  // white button
  &--white {
    color: $text-secondary;
    background-color: $white;
    border-color: $border;
    &:hover {
      color: $text;
      background-color: $hover;
      border-color: $border; }
    &:focus, &.focus {
      color: $text;
      background-color: darken($hover, 3%);
      border-color: darken($border, 3%); }
    &.disabled, &:disabled {
      color: $text;
      background-color: $white;
      border-color: $white; }
    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active {
      color: $text;
      background-color: $hover;
      border-color: $border; } }
  // acc button
  &--acc {
    color: $white;
    background-color: #28a745;
    border-color: #28a745;
    &:hover {
      color: $white;
      background-color: #28a745;
      border-color: #28a745; }
    &:focus, &.focus {
      color: $text;
      background-color: darken(#28a745, 3%);
      border-color: darken(#28a745, 3%); }
    &.disabled, &:disabled {
      color: $white;
      background-color: #28a745;
      border-color: #28a745; }
    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active {
      color: $white;
      background-color: #28a745;
      border-color: #28a745; } }
  // danger button
  &--danger {
    color: $white;
    background-color: $danger;
    border-color: $danger;
    &:hover {
      color: $white;
      background-color: $danger;
      border-color: $danger; }
    &:focus, &.focus {
      color: $text;
      background-color: darken($danger, 3%);
      border-color: darken($danger, 3%); }
    &.disabled, &:disabled {
      color: $white;
      background-color: $danger;
      border-color: $danger; }
    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active {
      color: $white;
      background-color: $danger;
      border-color: $danger; } }
  // link button
  &--link {
    color: $link;
    background-color: transparent;
    border-color: transparent;
    text-decoration: underline;
    opacity: 1;
    &:hover {
      color: $link;
      text-decoration: underline;
      background-color: transparent;
      border-color: transparent;
      opacity: 0.7; }
    &:focus, &.focus {
      color: $link;
      text-decoration: underline;
      background-color: transparent;
      border-color: transparent; }
    &.disabled, &:disabled {
      color: $link;
      background-color: transparent;
      border-color: transparent; }
    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active {
      color: $link;
      background-color: transparent;
      border-color: transparent;
      opacity: 0.7; } }
  // icon button
  &--icon {
    color: $link;
    background-color: transparent;
    border-color: transparent;
    opacity: 1;
    .btn--wrap {
      display: flex;
      align-items: center; }
    .icon,
    span {
      display: block; }
    .icon {
      width: 1.5rem;
      height: 1.5rem;
      img {
        display: block; }
      &:first-child {
        margin-right: 0.75rem; }
      &:last-child {
        margin-left: 0.75rem; }
      &:first-child:last-child {
        margin: 0; } }
    &:hover {
      color: $link;
      background-color: transparent;
      border-color: transparent;
      opacity: 0.8; }
    &:focus, &.focus {
      color: $link;
      background-color: transparent;
      border-color: transparent; }
    &.disabled, &:disabled {
      color: $link;
      background-color: transparent;
      border-color: transparent; }
    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active {
      color: $link;
      background-color: transparent;
      border-color: transparent; } } }
.btn--bold {
  @include font-500(); }
.btn--nopadding {
  padding-left: 0;
  padding-right: 0; }
.btn--block {
  display: block;
  width: 100%; }
.btn--block + .btn--block {
  margin-top: 0.5rem; }
.btn--back {
  color: $text;
  padding-left: 0;
  padding-right: 0;
  .icon svg circle {
    stroke: #7385A5;
    transition: all .3s ease; }
  &:hover {
    color: $link;
    opacity: 1;
    text-decoration: none;
    .icon svg circle {
      stroke: $link; } } }



// text alignment
.text-right {
  text-align: right; }
.text-left {
  text-align: left; }


// fade animation
.fade {
  transition: opacity 0.15s linear; }
.fade:not(.show) {
  opacity: 0; }


// collapse animation
.collapse:not(.show) {
  display: none; }
.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.15s ease; }


// tab
.tab-content > .tab-pane {
  display: none; }
.tab-content > .active {
  display: block; }


// dropdown
.dropdown {
  position: relative;
  display: inline-block; }
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 13.75rem;
  padding: 0.25rem 0;
  margin: 0.25rem 0;
  font-size: 1rem;
  color: $text;
  text-align: left;
  list-style: none;
  background-color: $white;
  background-clip: padding-box;
  border: 0;
  border-radius: 0.25rem;
  box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
  &.show {
    display: block; } }
.dropdown-menu-left {
  right: auto;
  left: 0; }
.dropdown-menu-right {
  right: 0;
  left: auto; }
.dropdown-item {
  display: block;
  width: 100%;
  padding: 0.5rem 1rem;
  clear: both;
  color: $text;
  text-align: inherit;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
  line-height: 1.5em;
  &:focus {
    outline: none !important; }
  &:hover,
  &.active {
    background: $hover;
    color: $text;
    text-decoration: none; } }


// search box
.search {
  display: inline-block;
  position: relative;
  max-width: 240px;
  &__icon {
    position: absolute;
    top: calc(50% - 0.75rem);
    left: 0.75rem;
    width: 1.5rem;
    height: 1.5rem;
    img, svg {
      display: block; }
    svg circle {
      stroke: #7385A5; } }
  .form-control {
    padding-left: 2.75rem; } }


// modal
.modal-open {
  overflow: hidden;
  .modal {
    overflow-x: hidden;
    overflow-y: auto; } }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0; }

.modal-dialog {
  position: relative;
  width: auto;
  margin: 0.5rem;
  pointer-events: none; }

.modal.fade .modal-dialog {
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -webkit-transform: scale(0.3);
  transform: scale(0.3); }

.modal.show .modal-dialog {
  transform: none; }

.modal.modal--static .modal-dialog {
  -webkit-transform: scale(1.02);
  transform: scale(1.02); }

.modal-dialog--scrollable {
  display: -ms-flexbox;
  display: flex;
  max-height: calc(100% - 1rem); }

.modal-dialog--scrollable .modal-content {
  max-height: calc(100vh - 1rem);
  overflow: hidden; }

.modal-dialog--scrollable .modal-header,
.modal-dialog--scrollable .modal-footer {
  -ms-flex-negative: 0;
  flex-shrink: 0; }

.modal-dialog--scrollable .modal-body {
  overflow-y: auto; }

.modal-dialog--centered {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  min-height: calc(100% - 1rem); }

.modal-dialog--centered::before {
  display: block;
  height: calc(100vh - 1rem);
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  content: ""; }

.modal-dialog--centered.modal-dialog--scrollable {
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  height: 100%; }

.modal-dialog--centered.modal-dialog--scrollable .modal-content {
  max-height: none; }

.modal-dialog--centered.modal-dialog--scrollable::before {
  content: none; }

.modal-content {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #ffffff;
  background-clip: padding-box;
  border-radius: 0.5rem;
  outline: 0; }

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  &.fade {
    opacity: 0; }
  &.show {
    opacity: .75; } }

.modal-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
  align-items: flex-start;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-bottom: 1px solid #dee2e6;
  border-top-left-radius: calc(0.5rem - 1px);
  border-top-right-radius: calc(0.5rem - 1px);
  .close {
    padding: 1rem 1rem;
    margin: -1rem -1rem -1rem auto; } }

.modal-title {
  margin-bottom: 0; }

.modal-body {
  position: relative;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 1.5rem; }

.modal-footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: end;
  justify-content: flex-end;
  padding: 0.75rem;
  border-top: 1px solid $border;
  border-bottom-right-radius: calc(0.5rem - 1px);
  border-bottom-left-radius: calc(0.5rem - 1px); }

.modal-footer > * {
  margin: 0.25rem; }

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll; }

.alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid $border;
  border-radius: 0.25rem;
  color: $text;
  &:first-child {
    margin-top: 2rem; }
  &--success {
    background-color: #d4edda;
    border-color: #c3e6cb; }
  &--danger {
    background-color: #f8d7da;
    border-color: #f5c6cb; }
  &--warning {
    background-color: #fff3cd;
    border-color: #ffeeba; } }

// Panel
.panel {
  position: relative;
  margin-bottom: 32px;
  background-color: #fff;
  border: 1px solid #D0D6DD;
  border-radius: 4px;
  &-header,
  &-body,
  &-footer {
    padding: 1.25rem; }
  &-header + &-body {
    padding-top: 0; }
  &-title {
    margin-bottom: 0; } }

// Summrary
.summary {
  &-label {
    font-size: 14px;
    letter-spacing: 1px;
    line-height: 24px;
    font-weight: 300;
    margin-bottom: 10px;
    text-transform: uppercase; }
  &-value {
    display: block;
    font-size: 42px;
    line-height: 50px;
    color: #37474f;
    font-weight: 400;
    margin-bottom: 10px;
    &-text {
      font-size: 18px;
      line-height: 26px;
      font-style: normal;
      display: inline-block;
      margin: 0 6px;
      opacity: .5; } } }

// Progress Bar
.progress {
  border-radius: 2px;
  height: 8px;
  overflow: hidden;
  font-size: .75rem;
  background-color: #e9ecef;
  display: flex;
  &-bar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    transition: width .6s ease;
    background-color: $primary; } }


// Datepicker
.form-group.dp {
  position: relative;
  .inpt-apend {
    width: 20px;
    height: 20px;
    display: block;
    background-image: url(../images/ic-date.svg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    top: 40px;
    right: 16px; } }

// Container Fluid

.container-fluid {
  width: calc(100% + 225px);
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: -225px; }



@media (min-width: 576px) {
  .modal-dialog {
    max-width: 500px;
    margin: 1.75rem auto; }
  .modal-dialog--scrollable {
    max-height: calc(100% - 3.5rem); }
  .modal-dialog--scrollable .modal-content {
    max-height: calc(100vh - 3.5rem); }
  .modal-dialog--centered {
    min-height: calc(100% - 3.5rem); }
  .modal-dialog--centered::before {
    height: calc(100vh - 3.5rem);
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content; }
  .modal-dialog--sm {
    max-width: 400px; } }

@media (min-width: 992px) {
  .modal-dialog--lg,
  .modal-dialog--xl {
    max-width: 800px; } }

@media (min-width: 1200px) {
  .modal-dialog--xl {
    max-width: 1140px; } }



// The prefers-reduced-motion CSS media feature is used to detect if the user has requested that the system minimize the amount of non-essential motion it uses.
@media (prefers-reduced-motion: reduce) {
  .btn {
    transition: none; }
  .fade {
    transition: none; }
  .collapsing {
    transition: none; }
  .modal.fade .modal-dialog {
    transition: none; } }
