.sidebar {
  display: block;
  width: 220px;
  height: 100vh;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  background: $white;
  border-right: 1px solid $border;
  padding-top: 5.0625rem;
  &__wrap {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%; }
  .main-menu {
    display: block;
    width: 100%;
    height: calc(100% - 96px);
    overflow-y: auto;
    &__link {
      display: block;
      position: relative;
      color: $text-secondary;
      background: $white;
      padding: 0.875rem 1.5rem 0.875rem 3.75rem;
      transition: all .3s ease;
      .icon {
        display: block;
        position: absolute;
        width: 1.5rem;
        height: 1.5rem;
        top: 0.875rem;
        left: 1.5rem;
        img,
        svg {
          display: block;
          max-width: 100%;
          max-height: 100%; }
        svg path {
          stroke: $text-secondary; } } }
    &__item {
      display: block;
      &:hover,
      &.active {
        .main-menu__link {
          text-decoration: none;
          color: $primary;
          background: $hover;
          @include font-500(); }
        svg path {
          stroke: $primary; } } } }

  .user-snapshot {
    display: flex;
    align-items: center;
    margin-top: auto;
    padding: 2rem 1.5rem;
    &__toggle {
      display: flex;
      align-items: center;
      cursor: pointer; }
    &__avatar {
      display: block;
      width: 100%;
      max-width: 2rem;
      flex: 0 0 2rem;
      img {
        display: blcok;
        width: 2rem;
        height: 2rem; } }
    &__wrap {
      display: block;
      width: 100%;
      max-width: calc(100% - 2rem);
      flex: 0 0 calc(100% - 2rem);
      padding-left: 0.625rem;
      span {
        display: block;
        &.username {
          font-size: 1rem;
          line-height: 1.5em;
          @include font-500(); }
        &.title {
          font-size: 0.875rem;
          line-height: 1.571em;
          @include font-400(); } } } } }
