/* Custom Plugins */

.bootstrap-select {
  position: relative;
  & > .dropdown-toggle {
    @include font-400();
    position: relative;
    color: $text;
    background: $white;
    border: 1px solid $border;
    padding: 0.4375rem 2.5rem 0.4375rem 0.75rem;
    &:after {
      content: "";
      display: block;
      width: 0.875rem;
      height: 0.875rem;
      background: url(../images/ic-down.svg);
      background-position: center;
      background-size: contain;
      position: absolute;
      right: 0.75rem;
      top: calc(50% - 0.4375rem); }
    &:hover {
      color: $text;
      background: $hover; }
    &:focus {
      color: $text;
      outline: none !important; } }
  .dropdown-menu {
    max-width: 100%; }
  .no-results {
    white-space: initial;
    word-break: break-all; }

  .bs-ok-default:after {
    border-width: 0 .1em .1em 0;
    border-color: $primary; }
  &.show-tick .dropdown-menu .selected span.check-mark {
    top: 0.5rem; } }


//summernote
.note-editor.note-frame {
  border: 1px solid $border;
  color: $text;
  @include font-400();
  .note-toolbar {
    background: #FAFBFD; }
  .note-btn {
    font-size: 0.75rem;
    line-height: 1.5em;
    color: $text-secondary;
    border-color: $border;
    background: $white;
    &:hover {
      background: $hover; } }
  .note-editing-area .note-editable a {
    color: $link;
    text-decoration: underline; }
  .note-editable {
    min-height: 27.5rem;
    u {
      text-decoration: underline; } }
  .sn-checkbox-open-in-new-window,
  .sn-checkbox-use-protocol {
    display: none; }
  .note-statusbar {
    display: none; }
  .dropdown-style {
    .note-dropdown-item {
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(7),
      &:nth-child(8),
      &:nth-child(9) {
        display: none; } } }
  .note-modal.open {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    min-height: calc(100% - 1rem); }
  .note-modal-content {
    background-color: #ffffff;
    background-clip: padding-box;
    border-radius: 0.5rem;
    outline: 0;
    border: 0;
    box-shadow: none;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    -webkit-transform: scale(0.3);
    transform: scale(0.3); }
  .note-modal.open .note-modal-content {
    transform: none; }
  .note-modal-header {
    padding: 1.5rem 1.5rem 1rem;
    border-radius: calc(0.5rem - 1px) calc(0.5rem - 1px) 0 0;
    border: 0; }
  .note-modal-title {
    font-size: 1.25rem;
    line-height: 1.5em;
    colro: $text;
    @include font-600(); }
  .note-modal-body {
    padding: 0 1.5rem;
    .note-form-label {
      font-size: 1rem;
      line-height: 1rem;
      font-weight: normal;
      color: $text;
      @include font-500();
      padding: 0;
      margin: 0 0 4px; } }
  .note-modal-footer {
    height: auto;
    text-align: right;
    padding: .5rem 1.5rem 1.5rem;
    border-radius: 0 0 calc(0.5rem - 1px) calc(0.5rem - 1px);
    .note-btn {
      font-size: 1rem;
      line-height: 1.5em;
      float: none; }
    .btn-primary {
      color: $white;
      background-color: $primary;
      border-color: $primary;
      &:hover {
        color: $white;
        background-color: darken($primary, 5%);
        border-color: darken($primary, 5%); }
      &:focus, &.focus {
        color: $white;
        background-color: darken($primary, 8%);
        border-color: darken($primary, 8%); }
      &.disabled, &:disabled {
        color: $muted;
        background-color: $white;
        border-color: $border;
        opacity: 1; }
      &:not(:disabled):not(.disabled):active,
      &:not(:disabled):not(.disabled).active {
        color: $white;
        background-color: darken($primary, 5%);
        border-color: darken($primary, 5%); } } } }
.note-modal-backdrop {
  background-color: #000;
  opacity: .75; }



