/* Contents */

main {
  display: block;
  width: calc(100% - 220px);
  min-height: 100vh;
  margin-left: 220px;
  padding-top: 5.0625rem;
  background: $white; }

.page-login {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background: #FAFBFD;
  min-width: 100%;
  min-height: 100vh;
  margin-left: -220px;
  margin-top: -5.0625rem;
  &__box {
    display: block;
    position: relative;
    max-width: 400px;
    width: 100%;
    margin: 5rem auto;
    background: #ffffff;
    box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
    border-radius: 0.5rem;
    padding: 2rem; }
  &__logo {
    margin-bottom: 1.5rem;
    img {
      display: block;
      // height: 3rem
      height: 8rem;
      width: auto;
      margin: 0 auto; } } }

.page-title {
  display: flex;
  align-items: center;
  padding: 1.5rem 2.5rem;
  background: $background;
  h1 {
    color: $primary;
    &:last-child {
      margin-bottom: 0; }
    .total {
      display: inline-block;
      margin-left: 0.75rem;
      color: $text-secondary;
      @include font-400(); } }
  &__left {
    width: 60%; }
  &__action {
    width: 40%;
    text-align: right; } }

.page-tabs {
  display: block;
  position: relative;
  padding: 0 2.5rem;
  background: $background;
  border-bottom: 1px solid $border;
  .nav {
    display: block;
    margin-left: -1rem;
    margin-right: -1rem;
    &__link {
      display: block;
      padding: 0.75rem 0;
      border-bottom: 2px solid transparent;
      color: $text-secondary;
      &:hover {
        color: $text;
        text-decoration: none; }
      &.active {
        color: $text;
        text-decoration: none;
        border-bottom-color: $primary; } }
    &__item {
      display: inline-block;
      margin: 0 1rem; } } }

.page-filter {
  display: block;
  position: relative;
  padding: 1.5rem 0;
  .row {
    margin-left: -4px;
    margin-right: -4px;
    &> .col,
    &> [class*="col-"] {
      padding-left: 4px;
      padding-right: 4px; } }
  .col-1 {
    max-width: 228px;
    flex: 0 0 228px; }
  .item:not(:last-child):not(.bulk-false) {
    margin-bottom: 2rem; }
  .btn--bulk {
    color: $text;
    min-width: 160px;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    .btn--wrap {
      display: flex;
      align-items: center; }
    span, .icon {
      display: block;
      vertical-align: middle; }
    .icon {
      margin-left: auto;
      img, svg {
        display: block;
        width: 0.875rem;
        height: 0.875rem; } } } }


.page-main {
  padding-left: 2.5rem;
  padding-right: 2.5rem; }


.table {
  padding-bottom: 1.5rem;
  &:first-child {
    padding-top: 1.5rem; }
  &:last-child {
    padding-bottom: 6.25rem; }
  table {
    width: 100%;
    border-collapse: collapse !important;
    td,
    th {
      border-bottom: 1px solid $border;
      padding: 0.75rem 1rem; }
    .custom-control {
      &-label {
        padding-left: 0; } }
    .check-all, .has-check {
      width: 52px;
      max-width: 52px;
      min-width: 52px; }
    .has-edit {
      padding-right: 0;
      width: 1.5rem; }
    .has-action {
      padding-right: 0;
      width: 10%;
      text-align: right;
      a {
        display: inline-block;
        vertical-align: middle;
        & + a {
          margin-left: .5rem; } } }
    .btn-edit {
      opacity: 0;
      visibility: hidden;
      img, svg {
        display: block;
        height: 1.5rem;
        width: auto; } }
    tr:hover {
      .btn-edit {
        opacity: 1;
        visibility: visible; } } }
  &__footer {
    padding-top: 1.5rem;
    text-align: right;
    span {
      display: inline-block;
      vertical-align: middle;
      margin: 0 1.125rem; }
    .prev,
    .next {
      display: inline-block;
      vertical-align: middle;
      opacity: 1;
      transition: all .3s ease;
      img,
      svg {
        display: block;
        width: 1.25rem;
        height: 1.25rem;
        object: contain; }
      svg circle {
        stroke: #7385A5; }
      &.disabled {
        display: none; }
      &:hover {
        opacity: .5; } } }
  .dataTables_wrapper {
    .col-md-6 {
      &:last-child {
        display: flex;
        justify-content: flex-end; } } }
  .dataTables_length {
    margin-bottom: 2.5rem;
    max-width: 228px;
    flex: 0 0 228px; }
  .dataTables_filter {
    max-width: 228px;
    flex: 0 0 228px; }
  .dataTable {
    margin-bottom: 2.5rem; }
  .dataTables_paginate {
    text-align: right;
    .pagination {
      li {
        display: inline-block;
        vertical-align: middle;
        margin: 0 1.125rem;
        a {
          color: #333A47; } } } } }


.add-contact {
  &:last-child {
    margin-bottom: 6.25rem; }
  &:first-child {
    padding-top: 1.5rem; }
  &__title {
    margin-bottom: 2.5rem;
    display: flex;
    // align-items: center
    // margin-bottom: 1rem
    h1 {
      color: $primary;
      margin-bottom: 0; }
    .btn--del {
      margin-left: auto; } }

  .data-tagihan {
    .info-panel {
      border: 1px solid #f5f5f5;
      background: #fcfcfc;
      padding: 6px 12px;
      border-radius: 4px;
      margin-bottom: 1.5em;
      label {
        display: flex;
        width: 100%;
        position: relative;
        padding: 6px 0;
        margin: 0;
        font-size: 14px;
        line-height: 1.2em;
        span {
          display: block;
          width: 100%;
          max-width: 40%;
          flex: 0 0 40%;
          color: #7385A5; }
        b {
          display: block;
          width: 100%;
          max-width: 40%;
          flex: 0 0 40%;
          padding-left: 15px; } } } } }

.emails {
  position: relative;
  &:last-child {
    margin-bottom: 6.25rem; }
  &:first-child {
    padding-top: 1.5rem; }
  &__title {
    margin-bottom: 2.5rem;
    h1 {
      color: $primary; } }
  .selected-multiple,
  .contact-group {
    display: none;
    &.show {
      display: block; }
    label {
      margin-bottom: 0.5rem; }
    .item {
      display: block;
      position: relative;
      padding: 1rem 2rem 1rem 0;
      border-bottom: 1px solid $border;
      &--template {
        display: none; }
      span.name {
        display: block;
        font-size: 1rem;
        line-height: 1.5em;
        @include font-400();
        color: $text; }
      .meta {
        color: $text-secondary;
        span {
          display: inline-block;
          font-size: 0.875rem;
          line-height: 1.571em;
          @include font-400();
          color: $text-secondary; } } }
    .btn--del {
      display: block;
      position: absolute;
      top: calc(50% - 0.75rem);
      right: 0;
      cursor: pointer;
      opacity: 1;
      transition: opacity .3s ease;
      img, svg {
        display: block;
        width: 1.5rem;
        height: 1.5rem;
        object-fit: contain; }
      &:hover {
        opacity: .5; } }
    &__action {
      padding-top: 1rem; } }

  .field-email {
    .note-editor.note-frame {
      margin-bottom: 1.5rem; }
    span.copy {
      display: block;
      font-size: 0.875rem;
      line-height: 1.571em;
      @include font-400();
      color: $text-secondary;
      text-align: center; }
    &__logo {
      display: block;
      margin-bottom: 1.5rem;
      text-align: center;
      img {
        display: inline-block;
        height: 3rem;
        width: auto; } }
    &__icons {
      text-align: center;
      a {
        display: inline-block;
        margin: 0 1rem 1rem;
        img, svg {
          display: block;
          width: 1.5rem;
          height: 1.5rem;
          object-fit: cover; }
        svg circle {
          stroke: $text-secondary; } } }
    &--preview {
      padding: 2rem 1.5rem 3rem;
      background: #FAFBFD;
      .field-email__content {
        background: #FFFFFF;
        border: 1px solid #C9CED7;
        border-radius: 4px;
        box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
        padding: 2.5rem;
        margin: 0.5rem 0 1.5rem; } } } }

.stepper {
  position: absolute;
  top: 11.25rem;
  left: 0;
  transition: all .3s ease-in-out;
  .no {
    position: absolute;
    top: 0;
    left: 0;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 0.75rem;
    background: #9BA8BF;
    font-size: 0.875rem;
    line-height: 1.5rem;
    color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s ease-in-out; }
  &__item {
    display: block;
    position: relative;
    margin-bottom: 1.5em;
    padding-left: 2rem;
    color: $text-secondary;
    &.active {
      color: $text;
      .no {
        background: $primary; } } } }

.form-steps {
  position: relative;
  overflow: hidden;
  &__item {
    display: none;
    position: absolute;
    opacity: 0;
    transform: none;
    // transition: transform .5s ease-in-out, opacity .5s ease-in-out
    &.active {
      display: block;
      position: relative;
      opacity: 1;
      transform: none; }
