// Login Pasien

body.login main {
  padding: 0; }
.auth-page {
  overflow-x: hidden;
  margin-top: -81px;
  .left-wrap,
  .right-wrap {
    min-height: 100vh;
    position: relative;
    -webkit-box-shadow: -4px 4px 18px -6px rgba(61,61,61,.8);
    -moz-box-shadow: -4px 4px 18px -6px rgba(61,61,61,.8);
    box-shadow: -4px 4px 18px -6px rgba(61,61,61,.8); }
  .image {
    background-size: cover;
    background-position: 90%;
    background-repeat: no-repeat;
    @include absfull;
    &.hidden {
      visibility: hidden; } }
  #bg_lg {
    transition: all .8s ease;
    opacity: 0;
    visibility: hidden;
    &.show {
      opacity: 1 !important;
      visibility: visible !important;
 } }      // z-index: 1
  p {
    text-align: center;
    font-size: 0.875rem;
    line-height: 1.5em;
    margin-bottom: 0.875rem;
    color: $primary; }
  .col-md-10,
  .col-md-2 {
    transition: all .8s; }

  .left-wrap {
    .icon {
      border-radius: 50%;
      width: 110px;
      height: 110px;
      display: block;
      position: absolute;
      right: 35.7%;
      top: 60px;
      &:hover {
        cursor: pointer; }
      &.hidden {
        visibility: hidden; }
      &:before,
      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: 0;
        box-shadow: 0 0 0 white;
        animation: pulse 2.5s infinite; }
      &:after {
        animation: bigPulse 2.5s infinite; } }
    .description {
      max-width: 300px;
      border: 1px solid #ffffff;
      background: #0054b3;
      position: absolute;
      top: 30px;
      left: 0;
      z-index: 3;
      padding: 20px;
      border-radius: 4px;
      color: #ffffff !important;
      transition: all .3s ease;
      transform: translateX(-100%);
      &.show {
        transform: translateX(20px); }

      h3 {
        font-size: 24px;
        font-weight: bold;
        text-align: center; }
      p {
        color: #ffffff !important; } } }


  .form-wrap {
    min-height: 100%;
    width: 340px;
    display: flex;
    align-items: center;
    text-align: center;
    transform: translateX(-190px);
    transition: all .8s;
    .box {
      width: 100%;
      padding: 2rem;
      border-radius: 3px;
      background-color: #ffffff;
      box-shadow: 6px 14px 51px 3px rgba(94, 94, 94, 0.42); }
    .forgot-pw {
      margin-top: .5rem;
      font-size: .9rem;
      line-height: 1.5em;
      text-align: right; }
    .form-group.empw {
      .form-control {
        position: relative;
        &:focus {
          z-index: 2; } }
      .form-control.top {
        border-radius: 3px 3px 0 0; }
      .form-control.bot {
        border-radius: 0 0 3px 3px;
        margin-top: -1px; } } }
  @include responsive(769px) {
    .left-wrap {
      min-height: 40vh; }
    .right-wrap {
      min-height: 60vh; }
    .form-wrap {
      width: 100%;
      transform: translateX(0) translateY(-100px);
      justify-content: center;
      .box {
        width: 60%; } } }
  @include responsive(426px) {
    .form-wrap {
      .box {
        width: 100%; } } } }


.login-error {
  opacity: 0;
  max-height: 0;
  visibility: hidden;
  border-radius: 0 0 4px 4px;
  background: $red;
  transition: all .5s;
  p {
    color: #ffffff;
    margin-bottom: 0;
    font-size: 0.875rem;
    line-height: 1.5em; } }

.login.lgn-error {
  .form-group {
    margin-bottom: 0; }
  .form-control {
    border: 1px solid $red; }
  .login-error {
    opacity: 1;
    max-height: 9999px;
    visibility: visible;
    margin-bottom: 15px;
    padding: 5px 10px; } }

.loading-lgn {
  .col-md-10 {
    flex: 0 0 8.333333%;
    max-width: 8.333333%;
    transition-delay: 1s; }
  .col-md-2 {
    flex: 0 0 100%;
    max-width: 100%;
    transition-delay: 1s; }
  .form-wrap {
    transform: translateX(50px);
    opacity: 0;
    visibility: hidden; } }

.pasien-daftar {
  .form-wrap {
    width: 100%;
    transform: translateX(0); } }
